.parent {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
}

.child {
  position: absolute;

  left:0; right:0;
  top:0; bottom:0;
  margin:auto;

  width: 80%;
  height: 80%;

  max-width:100%;
  max-height:100%;

  overflow:auto;

  /* centred text */
  text-align: center;
}



